.WorkoutType{
    position: relative;
    width: 100%;
    margin: 5px;
    min-width: -webkit-fit-content;
    min-width: -moz-fit-content;
    min-width: fit-content;
    height: -webkit-max-content;
    height: -moz-max-content;
    height: max-content;
    padding: 10px;
    border-radius: 5px;
    background: -webkit-gradient(linear, left top, left bottom, from(#3a3a3b), to(#1d1d1f));
    background: -webkit-linear-gradient(#3a3a3b, #1d1d1f);
    background: -o-linear-gradient(#3a3a3b, #1d1d1f);
    background: linear-gradient(#3a3a3b, #1d1d1f);
    color: rgb(236, 235, 230);
    text-transform: capitalize;
    list-style: none;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}

.form{
    width: 100%;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
}

.count{
    width: 50px;
    text-align: center;
    margin: 5px;
    border-radius: 5px;
    background: -webkit-gradient(linear, left top, left bottom, from(#7a7a78), to(#3d3d3d));
    background: -webkit-linear-gradient(#7a7a78, #3d3d3d);
    background: -o-linear-gradient(#7a7a78, #3d3d3d);
    background: linear-gradient(#7a7a78, #3d3d3d);
    border: none;
    outline: none;
}

.name{
    display: inline-block;
    text-align: center;
    width: 100%;
    max-width: 22px;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
    min-width: 140px;
}

.container{
    display: inline-block;
    position: absolute;
    right: 3px;
    width: 20px;
    height: 20px;
    border-radius: 50%;
}

.container:hover, 
.container:active{
    background-color: rgb(71, 70, 70);
}


