.Toolbar{
    position: fixed;
    z-index: 800;
    left: 0;
    top: 0;
    background-color: black;
    height: 10vh;
    width: 100%;
    color: rgb(184, 181, 177);
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    -webkit-tap-highlight-color: transparent;
}

h4{
    display: inline;
    top: 5px;
    left: 5px;
}

.nav{
    width: 100%;
    display: none;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
}

h3{
    display: inline-block;
    margin: 3vh auto;
    /* left: 5px; */
}


