.textbox{
    width: 80%;
    outline: none;
    background-color: inherit;
    border: none;
    color: azure;
}

.fakeTextBox{
    display: -ms-inline-flexbox;
    display: inline-flex;
    border-radius: 5px;
    width: 80%;
    padding: 7px;
    font: inherit;
    background: -webkit-gradient(linear, left top, left bottom, from(#6d706e), to(#474747));
    background: -webkit-linear-gradient(#6d706e, #474747);
    background: -o-linear-gradient(#6d706e, #474747);
    background: linear-gradient(#6d706e, #474747);
    -webkit-box-shadow: inset 0 -10px rgba(66, 65, 65, 0.87);
            box-shadow: inset 0 -10px rgba(66, 65, 65, 0.87);
    margin: 5px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}




