.WorkoutTypes{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    width: 50vw;
    min-width: -webkit-fit-content;
    min-width: -moz-fit-content;
    min-width: fit-content;
    background: -webkit-gradient(linear, left top, left bottom, from(#48494a), to(#232324));
    background: -webkit-linear-gradient(#48494a, #232324);
    background: -o-linear-gradient(#48494a, #232324);
    background: linear-gradient(#48494a, #232324);
    margin: 30px auto;
    padding: 30px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    border-radius: 10px;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    -webkit-tap-highlight-color: transparent;
}

p{
    -webkit-animation: p 3s ease;
            animation: p 3s ease;
}

@-webkit-keyframes p {
    0%{
        opacity: 0;
    }
    100%{
        opacity: 1;
    }
}

@keyframes p {
    0%{
        opacity: 0;
    }
    100%{
        opacity: 1;
    }
}


