.smallSpinner{
    display: inline-block;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    outline: none;
    border: none;
    color: white;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    -webkit-tap-highlight-color: transparent;
}


