.AddWorkoutType{
    position: absolute;
    padding: 5px;
    right: 5px;
    top: 5px;
    width: 30px;
    height: 30px;
    border-radius: 50%;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    -webkit-tap-highlight-color: transparent;
}

.AddWorkoutType:hover, 
.AddWorkoutType:active{
    background-color: rgb(71, 70, 70);
}


