.position{
	width: 100%;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	bottom: 1px;
	margin: 5px auto;
	width: 100%;
	outline: none;
}

.myButton {
	-webkit-box-shadow: 0px 1px 0px 0px #1c1b18;
	        box-shadow: 0px 1px 0px 0px #1c1b18;
	background:-webkit-gradient(linear, left top, left bottom, color-stop(5%, #c2eac8), to(#a6ccaa));
	background:-webkit-linear-gradient(top, #c2eac8 5%, #a6ccaa 100%);
	background:-o-linear-gradient(top, #c2eac8 5%, #a6ccaa 100%);
	background:linear-gradient(to bottom, #c2eac8 5%, #a6ccaa 100%);
	background-color:#97dab1;
	border-radius:15px;
	border: 2px solid #333029;
	display:block;
	cursor:pointer;
	color:#505739;
	font-family:Arial;
	font-size:14px;
	font-weight:bold;
	margin: auto;
	padding:4px;
	text-decoration:none;
	text-shadow:0px 1px 0px #ffffff;
	outline: none !important;
	-webkit-user-select: none;
	   -moz-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
	-webkit-tap-highlight-color: transparent;
}
.myButton:hover {
	background:-webkit-gradient(linear, left top, left bottom, color-stop(5%, #a6cca9), to(#c2eacb));
	background:-webkit-linear-gradient(top, #a6cca9 5%, #c2eacb 100%);
	background:-o-linear-gradient(top, #a6cca9 5%, #c2eacb 100%);
	background:linear-gradient(to bottom, #a6cca9 5%, #c2eacb 100%);
	background-color:#ccc2a6;
}
.myButton:active {
	position:relative;
	background:-webkit-gradient(linear, left top, left bottom, from(#aca697), to(#938d7d));
	background:-webkit-linear-gradient(#aca697, #938d7d);
	background:-o-linear-gradient(#aca697, #938d7d);
	background:linear-gradient(#aca697, #938d7d);
	top: 1px;
}

.myButton:disabled{
	cursor: no-drop;
	border: none;
	text-shadow: none;
	color: darkgrey;
	background: -webkit-gradient(linear, left top, left bottom, from(#5d5c5b), to(#585755));
	background: -webkit-linear-gradient(#5d5c5b, #585755);
	background: -o-linear-gradient(#5d5c5b, #585755);
	background: linear-gradient(#5d5c5b, #585755);
	color: rgb(64, 64, 64);
}


