.container{
    display: inline-block;
    position: absolute;
    right: 3px;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    -webkit-tap-highlight-color: transparent;
}

.container:hover, 
.container:active{
    background-color: rgb(71, 70, 70);
}

.delete{
    margin: auto;
}


