.Modal {
    position: fixed;
    color: cornsilk;
    z-index: 1000;
    background: -webkit-gradient(linear, left top, left bottom, from(#050508), to(#282829));
    background: -webkit-linear-gradient(#050508, #282829);
    background: -o-linear-gradient(#050508, #282829);
    background: linear-gradient(#050508, #282829);
    width: 70%;
    padding: 5px;
    -webkit-box-shadow: 1px 1px 1px black;
            box-shadow: 1px 1px 1px black;
    border-radius: 10px;
    left: 15%;
    top: 30%;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    -webkit-transition: all 0.3s ease-out;
    -o-transition: all 0.3s ease-out;
    transition: all 0.3s ease-out;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    -webkit-tap-highlight-color: transparent;
}

@media (min-width: 600px) {
    .Modal {
        width: 500px;
        left: calc(50% - 250px);
    }
}

.ModalOpen{
    -webkit-animation: openModal 0.5s ease-out forwards;
            animation: openModal 0.5s ease-out forwards;
}

.ModalClose{
    -webkit-animation: closeModal 0.5s ease-out forwards;
            animation: closeModal 0.5s ease-out forwards;
}
